@import '../common/styles/saas/variables';
@import '../common/styles/saas/mixins';

.contactsBlock {
  background-color: $bgSecondColor;
  padding: 100px 0;

  .container {
    @include container();
    @include flex(column, flex-start, stretch, nowrap);

    .contactsArea {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      width: 100%;

      .contactInfo {
        box-sizing: border-box;
        padding: 0;
        margin: 0;
        margin-left: 70px;
        color: #666;

        .infoItem {
          font-size: 16px;
          color: #666;

          .icon {
            width: 50px;
            height: 50px;
            line-height: 50px;
            text-align: center;
            font-size: 16px;
            float: left;
            background: #222;
            color: #eee;
            border-radius: 4px;
          }
          .infoContent{
            text-align: left;
            margin-left: 70px;
            color: #666;
          }
        }
      }

      .formStyle {
        overflow: hidden;

        input {
          width: 80%;
          padding: 10px;
          border: none;
          background: #f7f7f7;
          margin-bottom: 30px;
        }

        textarea {
          width: 80%;
          height: 140px;
          padding: 10px;
          border: none;
          background: #f7f7f7;
          margin-bottom: 30px;
          resize: none;
          &:-webkit-autofill{
            border: 1px solid #666666;
            -webkit-text-fill-color: #666666;
            -webkit-box-shadow: 0 0 0 1000px #666666 inset;
            transition: background-color 500s ease-in-out 0s;
            &:focus{
              border: 2px solid $bgSecondColor;
            }
          }
        }
      }

      @media screen and (max-width: 470px) {
        .contactsArea {
          width: 100%;
        }
      }
    }




    .contactsArea {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
    }
  }
}










